@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai|Permanent+Marker|Stylish&display=swap);
body {
	font-family: "Baloo Bhai", cursive !important;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: no-repeat center center fixed
		-webkit-gradient(linear, left bottom, right top, from(#051937), color-stop(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12));
	background: no-repeat center center fixed
		-webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
	background: no-repeat center center fixed
		linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
	background-size: cover;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}


.Mountain img {
	width: 100%;
	height: 70%;
	margin-bottom: 3rem;
	margin-top: 4rem;
	-webkit-filter: grayscale();
	        filter: grayscale();
	border-radius: 6%;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
	-webkit-transition: 0.7s -webkit-filter ease;
	transition: 0.7s -webkit-filter ease;
	transition: 0.7s filter ease;
	transition: 0.7s filter ease, 0.7s -webkit-filter ease;
}
.Mountain:hover img {
	-webkit-filter: none;
	        filter: none;
}

.Mountain .underline {
	text-decoration: none;
	border: 4px solid transparent;
	display: inline-block;
	vertical-align: top;
	line-height: 36px;
	text-transform: uppercase;
	color: black;
	letter-spacing: 0.2em;
	text-align: center;
	font-size: 2.5rem;
	margin: 10px;
	position: relative;
}

.underline::after {
	width: 0%;
	height: 2px;
	display: block;
	background-color: black;
	content: " ";
	position: absolute;
	left: 50%;
	-webkit-transition: left 0.7s cubic-bezier(0.215, 0.61, 0.355, 1),
		width 0.7s cubic-bezier(0.215, 0.61, 0.355, 1);
	transition: left 0.7s cubic-bezier(0.215, 0.61, 0.355, 1),
		width 0.7s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Mountain:hover .underline::after {
	width: 100%;
	left: 0;
}

.MountainInfo-card {
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}
.MountainInfo-card img {
	-webkit-filter: grayscale();
	        filter: grayscale();
	-webkit-transition: 0.7s -webkit-filter ease;
	transition: 0.7s -webkit-filter ease;
	transition: 0.7s filter ease;
	transition: 0.7s filter ease, 0.7s -webkit-filter ease;
}
.MountainInfo-card:hover img {
	-webkit-filter: none;
	        filter: none;
}

